import { Component, $$, Limiter, Link } from 'substance'
import SubstanceLogo from './SubstanceLogo'

export default class LogoOnlyHeader extends Component {
  render () {
    return $$('div', { class: 'sc-logo-only-header' },
      $$(Limiter, {},
        $$(Link, { inlineBlock: true, href: '/' },
          $$(SubstanceLogo, { size: 'large', inverted: this.props.inverted })
        )
      )
    )
  }
}
